<template>
  <div class="loading2">
      <div :style="'transform: rotate('+rotate+'deg)'">
          <img src="../assets/pull-icon.png">
      </div>
  </div>
</template>

<script>
  export default {
    name: 'loading2',
    data:function(){
      return {
        rotate:0
      }
    },
    mounted:function (){
      setInterval(() => {
        this.rotate += 72
      },100)
    }
  }
</script>

<style>
  .loading2{
    width: 100%;
    text-align: center;
    position: fixed;
    top: 0;
    bottom: 0;
    background: #fff;
  }
  .loading2 div{
    width: 80px;
    height: 70px;
    overflow: hidden;
    margin: 60vh auto 0;
    transition: .1s;
  }
  .loading2 img{
    margin-top: -80px;
  }
</style>



